import { useState,useEffect } from 'react';
import React from "react";
import useStyles from './styles'
import { TextField,Button,Typography,Paper } from "@mui/material";
import FileBase64 from 'react-file-base64';
import {useDispatch} from 'react-redux'
import { createPost,updatePost } from '../../actions/posts';
import { useSelector } from "react-redux"
import { useNavigate } from 'react-router-dom';



const Form = ({currentId,setCurrentId}) =>{
    const [postData,setPostData] = useState({
        title:'',message:'',tags:'',selectedFile:''
    })
    const post = useSelector((state) => (currentId ? state.posts.posts.find((message) => message._id === currentId) : null));
    const classes = useStyles();
    const navigate = useNavigate()

    const dispatch = useDispatch()

    const user = JSON.parse(localStorage.getItem('profile'))

    useEffect(()=>{
        if(post){
            setPostData(post)  
        } 
    },[post])
    

    const handleSubmit = (e)=>{
          e.preventDefault()  

          if (currentId === 0) {
            dispatch(createPost({...postData,name:user?.result?.name},navigate));
          } else {
            dispatch(updatePost(currentId,{...postData,name:user?.result?.name}));
          }
          clear()
    }


    const clear = ()=>{
        setCurrentId(0)
        setPostData({title:'',message:'',tags:'',selectedFile:''})
    }

    if(!user?.result?.name){
        return(
            <Paper className={classes.paper}>
                <Typography variant='h6' align='center'>
                    Please Sign In to create your own memories and like other's memories
                </Typography>
            </Paper>
        )
    }
    return(
        <Paper className={classes.paper} elevation={6} >
        <form autoComplete="off" noValidate className={`${classes.root} ${classes.form}`} onSubmit={handleSubmit}>
            <Typography variant="h6">{currentId ? `Editing "${post.title}"`: 'Creating a Memory'}</Typography>
            <TextField name="title" variant="outlined" label="Title" fullWidth value={postData.title} onChange={(e)=>setPostData({...postData,title:e.target.value})} />
            <TextField name="message" variant="outlined" label="Message" fullWidth value={postData.message} onChange={(e)=>setPostData({...postData,message:e.target.value})} />
            <TextField name="tags" variant="outlined" label="Tags (coma separated)" fullWidth value={postData.tags} onChange={(e) => setPostData({ ...postData, tags: e.target.value.split(',') })} />
            <div className={classes.fileInput}>
                <FileBase64 type='file' multiple={false} onDone={({base64})=>setPostData({...postData,selectedFile:base64})} />
            </div>
            <Button className={classes.buttonSubmit} variant='contained' fullWidth type='submit' color='primary' size='large'>Submit</Button>
            <Button className={classes.buttonSubmit} variant='contained' fullWidth onClick={clear} color='secondary' size='small'>Clear</Button>

        </form>

        </Paper>
    )
}

export default Form